<template>
  <form
    class="w-full max-w-screen-sm mx-auto mt-10"
    @submit.prevent="handleSubmit"
  >
    <FormInput
      :placeholder="$t('auth_input_account')"
      :error="accountError"
      :value="account"
      @onChange="handleChangeAccount"
    >
      <router-link to="/member/user/forget-account" class="underline">
        {{ $t("forget_account") }}
      </router-link>
    </FormInput>
    <FormInput
      :placeholder="$t('auth_input_password')"
      :error="passwordError"
      :value="password"
      @onChange="handleChangePassword"
      type="password"
    >
      <router-link to="/member/user/forget-password" class="underline">
        {{ $t("forget_password") }}
      </router-link>
    </FormInput>
    <div class="text-xl flex items-center gap-2.5 mb-9">
      <Checkbox :value="isRemember" @onChange="handleChangeRemember" />
      {{ $t("auth_remember") }}
    </div>
    <button
      class="rounded w-full py-2.5 lg:py-4 text-center text-white bg-button hover:bg-main-red text-xl lg:text-3xl mt-9"
    >
      {{ $t("user_sign_in") }}
    </button>
  </form>
</template>
<script>
import FormInput from "@/components/FormInput";
import Checkbox from "@/components/Checkbox";
import { mapActions } from "vuex";

export default {
  components: {
    FormInput,
    Checkbox,
  },
  data() {
    return {
      isRemember: false,
      account: "",
      accountError: "",
      password: "",
      passwordError: "",
    };
  },
  methods: {
    ...mapActions(["login"]),
    handleChangeRemember(value) {
      this.isRemember = value;
    },
    handleChangeAccount(value) {
      this.accountError = "";
      this.account = value;
    },
    handleChangePassword(value) {
      this.passwordError = "";
      this.password = value;
    },
    handleSubmit() {
      if (!this.account) {
        this.accountError = `*${this.$t("error_no_user")}`;
        return;
      }
      if (!this.password) {
        this.passwordError = `*${this.$t("error_password")}`;
        return;
      }
      this.login(this.isRemember);
      this.$router.push("/");
    },
  },
};
</script>
